.slider-container{padding:0;margin:3vw 0;position:relative;overflow:hidden;z-index:4;bottom:0;overflow:hidden}.slider{margin:0 4%}.slick-list{overflow:visible}.slider-container h2{margin:0 4%;margin-bottom:10px}.slider-carousel{justify-content:space-around;background:rgba(0,0,0,0)}.slick-arrow{height:100%;bottom:0}.slick-prev{left:-50px;z-index:2;content:"❰"}.slick-next{right:-25px;content:"❱"}.slick-next::before{content:""}.slick-prev::before{content:""}.slick-dots li.slick-active button:before{color:#fff}.slick-dots li button:before{color:#fff}.slider-carousel:hover .slick-next::before{font-weight:500;font-size:100px;width:2%;content:"❱"}.slider-carousel:hover .slick-prev::before{content:"❰";font-weight:200;font-size:100px;width:2%}